* {
  box-sizing: border-box;
  margin: 0;
}

html {
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
}

body {
  max-width: 1200px;
  margin: auto;
}

ul, li {
  margin: 0;
  padding: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav {
  padding: 23px 0;
}

.nav__list {
  list-style-type: none;
  display: flex;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  text-transform: uppercase;
  color: #060b35;
  padding: 23px 0;
  font-weight: 500;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.main {
  grid-gap: 46px 48px;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  padding: 50px 40px;
  display: grid;
}

.card {
  background-image: url("imac.202c52ff.jpeg");
  background-position: 19px 32px;
  background-repeat: no-repeat;
  background-size: 160px 134px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition-duration: .3s;
}

.card:hover {
  cursor: pointer;
  transition-duration: .3s;
  transform: scale(1.2);
}

.card__title {
  color: #060b35;
  padding: 174px 0 4px;
  font-weight: 500;
  text-decoration: none;
  display: block;
}

.card__title, .card__price {
  font-size: 12px;
  line-height: 18px;
}

.card:hover .card__title {
  color: #34568b;
}

.card__text {
  color: #616070;
  font-size: 10px;
  line-height: 14px;
}

.card__text--code {
  padding-bottom: 16px;
}

.card__stars {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 24px;
  display: flex;
}

.card__text--review {
  color: #060b35;
  text-decoration: none;
}

.card__wrap {
  justify-content: space-between;
  align-items: center;
  padding-bottom: 16px;
  display: flex;
}

.card__price {
  color: #616070;
  font-weight: 400;
}

.card__numb {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:last-child {
  background-image: url("star.822d561e.svg");
}

.stars__star:not(:last-child) {
  margin-right: 4px;
}

.buy {
  text-transform: uppercase;
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-radius: 5px;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 40px;
  text-decoration: none;
  display: block;
}

.buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
  height: 38px;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
